import React, { useLayoutEffect, useRef, useState } from "react";
import gsap from "gsap";
import TextPlugin from "gsap/TextPlugin";

import { motion, AnimatePresence } from "framer-motion"


import "./titlePage.css";
import bg from "../../img/titlePage/home-img.png";
import imgName from "../../img/titlePage/Name.png";
import Header from "../Header/Header";
import RunningText from "./RunningText/RunningText";
import animations from "../../animations/animations";

export default function TitlePage() {
  const [isOpen, setIsOpen] = useState(false);
  const ref = useRef();
  gsap.registerPlugin(TextPlugin);
  useLayoutEffect(() => {
    gsap.fromTo(
      ref.current,
      {
        text: {
          value: "   !",
          delimiter: "",
        },
        ease: "ease",
      },
      {
        delay: 5,
        duration: 3,
        text: {
          value: " ",
          delimiter: "",
        },
        ease: "ease",
      }
    );
  }, []);

  return (
    <>
    <motion.div className="coverPage"
    initial="hidden"
    animate="show"
    variants={animations}
    >
      <img src={bg} fill priority={true} alt="cover" />
    </motion.div>
    <div
      className="titlePage"
    >
      <div className="header">
        <div className="header_wrapper">
           <Header isOpen={isOpen} setIsOpen={setIsOpen}/>
        </div>
      </div>
      <AnimatePresence>
        <div className="bigLogo-container">
          <motion.img
          transition={{
            ease: "linear",
            duration: 10,
          }}
          initial={{ opacity: 0,scale:0 }}
        animate={{ opacity: 1,scale:1 }}
            className={isOpen?"bigLogo visibility":"bigLogo"}
             src={imgName} 
             alt="imgName" />
        </div>
             </AnimatePresence>
      <div className={isOpen?"titlePage-container visibility":"titlePage-container"}>
      <div className="titlePage_content">
           <motion.p 
            initial={{ opacity: 0,y:300 }}
            animate={{ opacity: 1,y:0 }}
            transition={{
              ease: "linear",
              duration: 1,
            }}
           ref={ref} className="titlePage_content_name_subtitle">
             
          </motion.p>
         </div>
         <RunningText/>
       </div>
        
</div>   
</>
  );
}
