import React from 'react'
import Marquee from "react-fast-marquee";
import {motion} from "framer-motion"

export default function RunningText() {
  return (
    <motion.nav
    initial={{ opacity: 0 }}
            animate={{ opacity: 1}}
            transition={{
              ease: "linear",
              duration: 10,
              delay:8,
            }}
    > 
    <Marquee pauseOnHover>
     <ul className="companySkills_items">
        <li> </li>
       <li> </li>
       <li> </li>
       <li> </li>
       <li> </li>
       <li> </li>
       <li>     </li>
     </ul>
      </Marquee>
   </motion.nav>
  )
}

