import gsap from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import React, { useLayoutEffect} from 'react'

import slider1 from "../../img/slider1.jpg"
import slider2 from "../../img/slider2.jpg"
import slider3 from "../../img/slider3.jpg"
// import slider4 from "../../img/slider5.jpg"
// import slider5 from "../../img/slider7.jpg"
import "./scrollSlider.css"

export default function ScrollSlider() {
 
    gsap.registerPlugin(ScrollTrigger);
    useLayoutEffect(()=>{

        let dataAnime = gsap.context(()=>{
            gsap.set(".imageScroll:not(:first-child)",{opacity:0,scale:0});
            const animeWay = gsap.to(".imageScroll:not(:first-child)",{opacity:1,scale:1,delay:30,duration:32,stagger:50});
       
        ScrollTrigger.create({
            start:"top top",
            end:"bottom bottom",
            pin:".imagesContainer",
            animation:animeWay,
            scrub:true,
            trigger:".cardsArray",
        })
       })   
       return ()=>dataAnime.revert()
    },[])
    return (
    
        <div className="cardsArray">
            <div className="textCard" >
<div className="cardAbout">
     
    <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Totam quia corrupti dolores suscipit excepturi magni ad ex magnam vero quo,molestiae pariatur illum facilis, blanditiis aspernatur reprehenderit architecto, tempore dolore.
    </p>
</div>
<div className="cardAbout">
     
    <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores saepe aliquid recusandae, esse quas blanditiis modi harum exercitationem nemo sequi omnis tenetur assumenda, delectus quibusdam inventore animi a repellat hic!
    </p>
</div>
<div className="cardAbout">
     
    <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Ratione doloremque alias suscipit, repellat sit porro, inventore enim maiores, iste iure ipsa vitae officiis minus nobis culpa consequuntur voluptatem aspernatur nulla.
    </p>
</div>
            </div>
            <div className="imagesContainer">
<div className="imagesWrapper">
<div className="imageScroll">
    <img src={slider1} alt='img'/>
</div>
<div className="imageScroll">
    <img src={slider2} alt='img'/>
</div><div className="imageScroll">
    <img src={slider3} alt='img'/>
</div>
{/* <div className="imageScroll">
    <img src={slider4} alt='img'/>
</div><div className="imageScroll">
    <img src={slider5} alt='img'/>
</div> */}
</div>
            </div>
        </div>
  )
}
