import React, { useEffect, useRef } from "react";
import { motion,useAnimate, stagger } from "framer-motion";

import logoImg from "../../img/titlePage/statusLogo.png"
import "./header.css";

const staggerMenuItems = stagger(0.1, { startDelay: 0.15 });
function useMenuAnimation(isOpen) {
  const [scope, animate] = useAnimate();

  useEffect(() => {
    animate(
      "ul",
      {
        clipPath: isOpen
          ? "inset(0% 0% 0% 0% round 10px)"
          : "inset(10% 50% 90% 50% round 10px)",
      },
      {
        type: "spring",
        bounce: 0,
        duration: 0.5,
      }
    );
    animate(
      "li",
      isOpen
        ? { opacity: 1, scale: 1, filter: "blur(0px)" }
        : { opacity: 0, scale: 0.3, filter: "blur(25px)" },
      {
        duration: 0.25,
        delay: isOpen ? staggerMenuItems : 0,
      }
    );
  }, [isOpen, animate]);
  return scope;
}

export default function Header({isOpen,setIsOpen}) {
  const ref=useRef();
  const scope = useMenuAnimation(isOpen);
  return (
    <>
      <div className={isOpen ? "menuContainer menuOpen" : "menuContainer"}>
        <div className="menuHeader">
          <div className="menuHeaderWrapper">
             <motion.div className="header_logo"
             initial={{ opacity: 0,y:-200 }}
             animate={{ opacity: 1,y:0 }}
             transition={{
               ease: "linear",
               duration: 1,
               delay:8
             }}
             >
            <a href="/">
              <img src={logoImg} alt="logoImage" />
            </a>
          </motion.div>
          <motion.div className="header_menu"
          initial={{ opacity: 0,y:-100 }}
          animate={{ opacity: 1,y:0 }}
          transition={{
            ease: "linear",
            duration: 1,
            delay:9
          }}
          >
            <nav>
              <ul ref={ref} className={isOpen?"header_menu_items visibility":"header_menu_items"}>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </nav>
            <div className="menuHeader_expandMenuWrapper">
              <div
                id="hamburger_menu"
                className={`${isOpen ? "open" : ""}`}
                onClick={() => setIsOpen(!isOpen)}
              >
                <div className="hamburger_menu_menu"></div>
                <span></span>
              </div>
            </div>
             </motion.div>
          </div>
        </div>
        <div className={isOpen ? "menuItems" : "visibility"}>
          <div className="menuItemsWrapper">
            <nav className="menu" ref={scope}>
              <ul
                style={{
                  pointerEvents: isOpen ? "auto" : "none",
                  clipPath: "inset(10% 50% 90% 50% round 10px)",
                }}
                className="menuItems_items"
              >
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </>
  );
}
